@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.wrapper{background:black;padding:30px 100px;min-height:100vh}@media screen and (max-width: 600px){.wrapper{padding:30px}}.header-row{padding:15px;text-align:center;background-color:#d3d3d3;margin-bottom:10px}@media screen and (max-width: 600px){.header-row{background-color:white}}.refresh-button-panel{text-align:center}.refresh-button-panel i{position:absolute;display:none}.refresh-button-panel button{background-color:#d8d8d8;border:0px;color:black;border-radius:10px;transition:0.4s;border:0}.refresh-button-panel button:hover{background-color:#00ccff81;color:#ffffff;border-radius:30px}.data-panel{margin:0px 20px;text-align:center;color:#d8d8d8}.data-panel .column{float:left;width:50%;padding:10px;border:10px;border-color:white}.data-panel .chart{margin:0px 20%}.data-panel .row:after{content:"";display:table;clear:both}.data-panel .header{font-weight:600;font-size:28px;text-align:center}.data-panel .title{padding-bottom:0px}.data-panel .title span{font-size:28px;font-weight:600;align-items:center}@media screen and (max-width: 600px){.data-panel .column{width:100%}.data-panel .chart{margin:0px}}.wrapper-box{padding:15px}

